.navbar-brand:link,
.navbar-brand:visited,
.navbar-brand:hover,
.navbar-brand:active {
    color: white;
    cursor: pointer;
}

.headerWrapper {
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 100;
}

.heading {
    position: relative;
    min-height: 100px;
    background-color: #36a;
    color: white;
}

.footer {
    width: 100%;
    position: fixed;
    bottom: 0px;
    z-index: 200;
    background-color: white;
    text-align: center !important;
}

a, a:visited, a:hover, a:active {
    color: #7af;
    text-decoration: none;
}

.viewPort {
    position: fixed;
    width: 100vw;
    top: 100px;
    bottom: 25px;
}

.scrollable {
    width: 100%;
    height: 100%;
    overflow: scroll;
}

.node {
   width: 150px !important;
   margin: 0;

   -moz-user-select:-moz-none;
   -moz-user-select:none;
   -o-user-select:none;
   -khtml-user-select:none;
   -webkit-user-select:none;
   -ms-user-select:none;
   user-select:none;
}

body {
    font-family: Arial, sans-serif;
    font-size: 1rem;
}

div.nodePerson {
   line-height: 1.0;
   flex-wrap: unset;
}

.stepMother,
.stepFather,
.siblingParents,
.siblingChildren,
.vcenter {
   display: inline-flex;
   align-items: center;
}

.nodeFather::before {
    content: "";
    position: absolute;
    height: 50%;
    border-left: 1px solid black;
    border-top: 1px solid black;
    top: 50%;
    width: 5px;
}

.nodeMother::before {
    content: "";
    position: absolute;
    height: 50%;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    width: 5px;
}

.nodeParents::before {
    content: "";
    position: absolute;
    height: 50%;
    border-bottom: 1px solid black;
    width: 5px;
    left: 25px;
}

.popover-body {
    width: min(400px, 90vw);
    background-color: #eeeeff;
}

.popover>.arrow::after {
    border-left-color: #eeeeff !important;
    border-right-color: #eeeeff !important;
}
.popover {
    max-width: min(400px, 90vw);
    text-align: center;
    line-height: 1.0;
}

.siblingPerson {
    padding: 0;
}

.siblingChildren .siblingPerson::after,
.childrenByMother .siblingPerson::after,
.childrenByFather .siblingPerson::after {
    content: "";
    position: absolute;
    height: 50%;
    border-top: 1px solid black;
    top: 50%;
    width: 10px;
    right: -1px;
}

.siblingParents>.container-fluid>.row:first-child .siblingPerson::before {
    content: "";
    position: absolute;
    height: 50%;
    border-top: 1px solid black;
    border-left: 1px solid black;
    top: 50%;
    width: 10px;
    left: -20px;
}

.siblingParents>.container-fluid>.row:last-child .siblingPerson::before {
    content: "";
    position: absolute;
    height: 50%;
    border-bottom: 1px solid black;
    border-left: 1px solid black;
    top: 0;
    width: 10px;
    left: -20px;
}

.siblingParents::before {
    content: "";
    position: absolute;
    height: 50%;
    border-top: 1px solid black;
    top: 50%;
    width: 10px;
    left: -14px;
}

.stepMother::before,
.stepFather::before {
    content: "";
    position: absolute;
    height: 50%;
    border-top: 1px solid black;
    top: 50%;
    width: 10px;
    left: -14px;
}

.stepMother::after {
    content: "";
    position: absolute;
    height: 50%;
    border-top: 1px solid black;
    border-left: 1px dotted black;
    top: 50%;
    width: 10px;
    left: -4px;
}

.stepFather::after {
    content: "";
    position: absolute;
    height: 50%;
    border-bottom: 1px solid black;
    border-left: 1px dotted black;
    top: 0;
    width: 10px;
    left: -4px;
}

.childrenByFather .row:not(:first-child) .siblingPerson>div::after,
.childrenByMother .row:not(:first-child) .siblingPerson>div::after,
.siblingChildren .row:not(:first-child) .siblingPerson>div::after {
    content: "";
    position: absolute;
    height: 50%;
    border-right: 1px solid black;
    top: 0px;
    width: 10px;
    right: -1px;
}


.childrenByFather .row:not(:last-child) .siblingPerson>div::before,
.childrenByMother .row:not(:last-child) .siblingPerson>div::before,
.siblingChildren .row:not(:last-child) .siblingPerson>div::before {
    content: "";
    position: absolute;
    height: 50%;
    border-right: 1px solid black;
    border-top: 1px solid black;
    top: 50%;
    width: 10px;
    right: -1px;
}

.detailsHolder {
    justify-content: flex-end;
    display: flex;
}

.details {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: min(450px, 100vw);
    z-index: 100;
    opacity: 0.6;
    background-color: #137;
    color: white;
}

.detailsFacts {
    height: calc(100vh - 450px);
    padding-bottom: 50px;
    overflow-x: hidden;
    overflow-y: auto;
}

.factYear {
    margin-left: -10px;
    margin-right: 10px;
}

.closeDetails {
    position: absolute;
    top: 5px;
    right: 10px;
    color: white;
    background: red;
    text-shadow: 0 1px 0 #a55;
    z-index: 1;
}

.previewBox {
    margin: auto;
    max-height: 20vh;
    max-width: min(80vw, 400px);
    display: block;
    padding-bottom: 15px;
}

.closeGallery {
    position: absolute;
    top: 15px;
    right: 15px;
    color: white;
    background: red;
}

.plusGallery {
    position: absolute;
    top: 80px;
    right: 15px;
    color: white;
    background: black;
}

.carousel-control-next-icon {
    background-color: black;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='-5 0 16 8'%3e%3cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3e%3c/svg%3e");
}

.carousel-control-prev-icon {
    background-color: black;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='-3 0 16 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3e%3c/svg%3e");
}

.carousel-control-next-icon,
.carousel-control-prev-icon,
button.fab {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: 0;
    opacity: 0.6;
}

button.fab:hover {
    opacity: 1;
}

button.fab:focus {
    outline: none;
}

.minusGallery {
    position: absolute;
    top: 145px;
    right: 15px;
    color: white;
    background: black;
}

.galleryModal {
    max-width: 80vw;
    max-height: 90vh;
    width: 90vw;
    height: 90vh;
}

.galleryContent {
    background-color: transparent;
    height: 100%;
    width: 100%;
    border: none;
}

.galleryItem {
    height: 90vh;
    width: 100%;
    overflow: hidden;
}

.galleryImageX0 {
    width: 50%;
    margin: auto;
}

.galleryImageX1 {
    width: 100%;
    margin: auto;
}

.galleryImageX2 {
    width: 200%;
    margin: auto;
}

.galleryImageX3 {
    width: 300%;
    margin: auto;
}

.galleryImageX4 {
    width: 400%;
    margin: auto;
}

.galleryImageX5 {
    width: 500%;
    margin: auto;
}

.carousel-caption {
    background-color: #000;
    opacity: 0.6;
}

.carousel-caption,
.carousel-control-next,
.carousel-control-prev,
.carousel-indicators {
   -moz-user-select:-moz-none;
   -moz-user-select:none;
   -o-user-select:none;
   -khtml-user-select:none;
   -webkit-user-select:none;
   -ms-user-select:none;
   user-select:none;
}

.rbt-input {
    width: 300px;
}

.rbt-menu {
    overflow: hidden auto !important;
}